.programs{
    margin: 80px auto;
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.program img{
    width: 100%;
    border-radius: 10px;
    display: block;

}
.program{
    flex-basis: 31%;
    position: relative;

}
.caption{
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    right:0;
    bottom:0;
    background: rgba( 0,15,152,0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color:#fff;
    cursor: pointer;
    opacity: 0;
    padding-top: 70%;
    transition: 0.4s;


}
figcaption {
    
    color: #000F38;
   font-size: 20px;
    margin-top: 2px;
    padding: 7px;
    text-align: center;
  }

.caption img{
    width: 60px;
    margin-bottom: 10px;
}
.program:hover .caption{
    opacity: 1;
    padding-top: 0;

}