@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Outfit", sans-serif;
    
    
}

body{
    background:#f5f5f5;
   
   
}

a{
    text-decoration: none;
    color: inherit;
    line-height: 1;
    cursor: pointer; 
    font-size: 18px;
}
.container{
    padding-left: 10%;
    padding-right: 10%;
}
.btn{
    background: #fff;
    color: #212121;
    padding: 14px 25px;
    font-size: 16px;
    border-radius: 30px;
    cursor: pointer;
    border: 0;
    outline: 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;

}
.btn img{
    width: 20px;
    margin-left: 13px ;
}
.btn.dark-btn{
    background: #212EA0;
    color: white;
}
